@import url(https://fonts.googleapis.com/css2?family=Dokdo&family=Montserrat:wght@100;300;400;600&display=swap);
/* 
--lightest: #e4d4c8;
--light: #ac9485;
--medium: #8e7161;
--dark: #29190e;
--highlight: #800000;
font-family: 'Dokdo', cursive;
*/

body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #29190e;
  color: #e4d4c8;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #e4d4c8;
}

:root {
  --lightest: #e4d4c8;
  --light: #ac9485;
  --medium: #8e7161;
  --dark: #29190e;
  --highlight: #800000;
}

.monster-list {
  display: grid;
  grid-auto-flow: column;
  grid-gap: 5px 15px;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(67, 1fr);
  margin: auto;
  max-width: 900px;
}

.monster-tag {
  align-items: center;
  background-color: #800000;
  background-color: var(--highlight);
  border-radius: 5px;
  box-shadow: -1px -1px 3px #ac9485,
    3px 3px 5px #29190e;
  box-shadow: -1px -1px 3px var(--light),
    3px 3px 5px var(--dark);
  color: #e4d4c8;
  color: var(--lightest);
  display: flex;
  font-size: medium;
  font-weight: 400;
  height: 80%;
  justify-content: center;
  margin: .3em;
  padding: .3em;
  text-shadow: 3px 3px 4px #29190e;
  text-shadow: 3px 3px 4px var(--dark);
  transition: 200ms;
}

.monster-tag:hover {
  background-color: #680101;
  color: #e4d4c8;
  color: var(--lightest);
  cursor: pointer;
  font-weight: 600;
  transform: scale(1.03);
  transition: 200ms;
}

.monster-tag:active {
  border-top: #ac9485 solid 1px;
  border-left: #ac9485 solid 1px;
  border-bottom: none;
  border-right: none;
}

@media only screen and (max-width: 500px) {
  .monster-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90%;
  }

  .monster-tag {
    display: flex;
    font-size: large;
    font-weight: 500;
    height: 25px;
    width: 300px;
  }
} 

/* -----TABLET----- */
@media only screen and (min-width: 501px) and (max-width: 1199px) {
  .monster-list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 90%;
    height: 8600px;
  }

  .monster-tag {
    display: flex;
    font-size: large;
    font-weight: 500;
    height: 25px;
    width: 200px;
  }
}
:root {
    --lightest: #e4d4c8;
    --light: #ac9485;
    --medium: #8e7161;
    --dark: #29190e;
    --highlight: #800000;
}

.monster-stats {
    color: #e4d4c8;
    color: var(--lightest);
    background-color: #8e7161;
    background-color: var(--medium);
    border-radius: 20px;
    box-shadow: -2px -2px 5px #e4d4c8, 
        5px 5px 8px black;
    box-shadow: -2px -2px 5px var(--lightest), 
        5px 5px 8px black;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-auto-rows: -webkit-max-content;
    grid-auto-rows: max-content;
    margin: auto auto 2em auto;
    max-width: 1000px;
    padding: 1em 2.5em;
    text-align: left;
    width: 80%;
}

.one-stat {
    background-color: #800000;
    background-color: var(--highlight);
    border-radius: 5px;
    box-shadow: -1px -1px 3px #ac9485,
        3px 3px 5px #29190e;
    box-shadow: -1px -1px 3px var(--light),
        3px 3px 5px var(--dark);
    color: #e4d4c8;
    color: var(--lightest);
    font-size: medium;
    font-weight: 400;
    margin: .3em;
    padding: .3em;
    text-shadow: 3px 3px 4px #29190e;
    text-shadow: 3px 3px 4px var(--dark);
}

.stat-detail {
    background-color: #29190e;
    background-color: var(--dark);
    border-radius: 5px;
    box-shadow: 1px 1px 3px #e4d4c8, -3px -3px 5px black;
    box-shadow: 1px 1px 3px var(--lightest), -3px -3px 5px black;
    color: #e4d4c8;
    color: var(--lightest);
    font-size: medium;
    line-height: 1.6;
    margin: .5em .5em .5em 1.5em;
    padding: .5em;
    text-shadow: 3px 3px 4px black;
}

.one-stat span {
    font-size: small;
}

.name-box {
    grid-column: 1 / 3;
    grid-row: 2;
}

.size-box {
    grid-column: 3 / 5;
    grid-row: 2;
}

.modifier-box {
    grid-column: 5;
    grid-row: 2;
}

.immunities-box {
    grid-column: 1 / 3;
    grid-row: 3;
}

.actions {
    grid-column: 3 / 6;
    grid-row: 3;
}

.legendary {
    grid-column: 3 / 6;
    grid-row: 4;
}

.special-abilities {
    grid-column: 1 / 3;
    grid-row: 4;
}

.add-button {
    background-color: #800000;
    background-color: var(--highlight);
    border-radius: 10px;
    box-shadow: -1px -1px 3px #e4d4c8,
        3px 3px 5px #29190e;
    box-shadow: -1px -1px 3px var(--lightest),
        3px 3px 5px var(--dark);
    box-sizing: content-box;
    color: #e4d4c8;
    color: var(--lightest);
    font-weight: 700;
    grid-column: 3;
    grid-row: 1;
    height: 35px;
    margin: 0 0 1em 0;
    place-self: center;
    width: 200px;
}

.add-button:hover {
    cursor: pointer;
}

.add-button:active {
    background-color: #e4d4c8;
    background-color: var(--lightest);
    color: #29190e;
    color: var(--dark);
    outline: none;
    box-shadow: -1px -1px 3px #29190e,
        3px 3px 5px #ac9485 inset;
    box-shadow: -1px -1px 3px var(--dark),
        3px 3px 5px var(--light) inset;
}

.add-button:focus {
    outline: none;
}

