      3px 3px 5px #29190e;
    box-shadow: -1px -1px 3px var(--light),
      3px 3px 5px var(--dark);
    font-size: large;
    height: 25px;
    width: 90%;
  }

  .delete-button {
    border: none;
    box-shadow: -1px -1px 3px #ac9485,
      3px 3px 5px #29190e;
    box-shadow: -1px -1px 3px var(--light),
      3px 3px 5px var(--dark);
    height: 35px;
    font-size: medium;
    left: 25px;
    margin: .3em;
    position: relative;
    width: 50%;
  }

  .delete-button:hover .delete-tooltip {
    visibility: hidden;
  }
}
.App {
  text-align: center;
}



:root {
  --lightest: #e4d4c8;
  --light: #ac9485;
  --medium: #8e7161;
  --dark: #29190e;
  --highlight: #800000;
}

.splash-title {
  font-family: 'Dokdo', sans-serif;
  font-size: 69px;
  margin: 0 auto 50px auto;
  text-shadow: 5px 5px 2px #29190e;
  text-shadow: 5px 5px 2px var(--dark);
  transform: scaleY(1.8);
}

.go-home {
  background-color: #800000;
  background-color: var(--highlight);
  border: 2px solid #ac9485;
  border: 2px solid var(--light);
  border-radius: 10px;
  color: #e4d4c8;
  color: var(--lightest);
  font-family: 'Montserrat', sans-serif;
  font-size: x-large;
  font-weight: 700;
  grid-column: 2;
  height: 100%;
  margin: auto;
  width: 20%;
  padding: 0 2rem;
}
