:root {
  --lightest: #e4d4c8;
  --light: #ac9485;
  --medium: #8e7161;
  --dark: #29190e;
  --highlight: #800000;
}

.header { 
  align-items: center;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  font-family: 'Montserrat', sans-serif;
  font-size: x-large;
  font-weight: 600;
  height: 290px;
  justify-content: center;
  margin: auto;
  width: 1200px;
}

.title {
  align-items: center;
  background-color: var(--highlight);
  border-radius: 0 0 20px 20px;
  display: flex;
  font-family: 'Dokdo', sans-serif;
  justify-content: space-around;
  width: 1090px;
}

h1 {
  position: relative;
  margin: 0;
}

.app-name {
  font-size: 38px;
  margin: 0 auto;
  text-shadow: 5px 5px 2px var(--dark);
  transform: scaleY(1.8);
}

.dragon {
  height: 200px;
  width: auto;
}

.dice {
  height: 25px;
  width: auto;
}

.nav-button {
  height: 50px;
  margin: 0 .5em;
  width: 250px;
}

.nav-button :hover {
  cursor: pointer;
}

.nav-button :active {
  background-color: var(--lightest);
  color: var(--dark);
}

/* -----NAVIGATION----- */
.nav {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  padding: .8em 0;
}

/* -----SEARCH----- */
.search-field {
  display: flex;
  grid-column: 1;
  margin: 0 2px 0 2px;
  width: 100%;
}

.search-input {
  background-color: var(--lightest);
  border: 2px solid var(--light);
  border-radius: 10px 0 0 10px;
  border-right: 0;
  cursor: text !important;
  margin: 0;
  padding: 0 0 0 10%;
  width: 100%;
}

::placeholder {
  color: var(--dark);
  font-size: medium;
}

.search-btn {
  background-color: var(--highlight);
  border: 2px solid var(--light);
  border-left: 0;
  border-radius: 0 10px 10px 0;
  color: var(--lightest);
  height: 50px;
  margin: 0;
  z-index: 1;
}

/* -----SHOW ALL----- */
.show-all-btn {
  background-color: var(--highlight);
  border: 2px solid var(--light);
  border-radius: 10px;
  color: var(--lightest);
  font-family: 'Montserrat', sans-serif;
  font-size: x-large;
  font-weight: 700;
  grid-column: 2;
  height: 100%;
  margin: 0 2px 0 2px;
  width: 100%;
}

/* -----FILTER----- */
.filter-form {
  display: flex;
  grid-column: 3;
  height: 100%;
  margin: 0 2px 0 2px;
}

.filter-label {
  align-items: center;
  background-color: var(--highlight);
  border: 2px solid var(--light);
  border-radius: 10px 0 0 10px;
  border-right: 0;
  color: var(--lightest);
  display: flex;
  font-size: medium;
  justify-content: center;
  width: 100%;
  cursor: default !important;
}

.filter-select{
  align-self: center;
  background-color: var(--lightest);
  border-bottom: 2px solid var(--light);
  border-left: 0;
  border-right: 0;
  color: var(--dark);
  height: 99%;
}

.filter-button {
  background-color: var(--highlight);
  border: 2px solid var(--light);
  border-left: 0;
  border-radius: 0 10px 10px 0;
  width: 45px;
}

/* -----ENCOUNTER----- */
.encounter-button {
  align-items: center;
  background-color: var(--highlight);
  border: 2px solid var(--light);
  border-radius: 10px;
  color: var(--lightest);
  display: flex;
  font-family: 'Montserrat', sans-serif;
  font-size: x-large;
  grid-column: 4;
  height: 91%;
  justify-content: center;
  margin: 0 2px 0 2px;
  width: 98%;
}

@media only screen and (max-width: 500px) {
  .header {
    font-size: medium;
    font-weight: 400;
    height: 200px;
    width: 95%;
  }
  
  .title {
    border-radius: 0 0 20px 20px;
    width: 100%;
    height: 100%;
  }
  
  .app-name {
    font-size: 15.5px;
    text-shadow: 2px 2px 1px var(--dark);
    height: 50%;
  }
  
  .dragon {
    height: 70px;
  }
  
  .nav {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: .8em;
    width: 400px;
  }
  
  .nav-button {
    height: 35px;
    width: 170px;
    justify-self: center;
    align-self: center;
  }

  /* -----SEARCH----- */
  ::placeholder {
    font-size: 12.5px;
  }

  .search-btn {
    height: 35px;
  }

  /* -----SHOW ALL----- */
  .show-all-btn {
    font-size: 16px;
    font-weight: 500;
  }

  /* -----FILTER----- */
  .filter-form {
    width: 100%;
  }

  .filter-label {
    font-size: 12.5px;
    width: 66%;
  }
  
  .filter-select {
    width: 34%;
    border: none;
  }

   .filter-button {
    height: 35px;
  }

  /* -----ENCOUNTER----- */
  .encounter-button {
    font-size: 16px;
  }
}

/* -----TABLET----- */
@media only screen and (min-width: 501px) and (max-width: 1199px){

  .header {
      font-weight: 400;
      height: 220px;
      width: 95%;
    }

    .title {
      width: 100%;
      height: 100%;
    }

    .app-name {
      font-size: 17px;
      text-shadow: 2px 2px 1px var(--dark);
    }

    .dragon {
      height: 85px;
    }

    .nav {
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr;
      gap: .8em;
      width: 400px;
    }

    .nav-button {
      height: 35px;
      width: 170px;
      justify-self: center;
      align-self: center;
    }

    /* -----SEARCH----- */
    ::placeholder {
      font-size: 12.5px;
    }

    .search-btn {
      height: 35px;
    }

    /* -----SHOW ALL----- */
    .show-all-btn {
      font-size: 16px;
      font-weight: 500;
    }

    /* -----FILTER----- */
    .filter-form {
      width: 100%;
    }

    .filter-label {
      font-size: 12.5px;
      width: 66%;
    }

    .filter-select {
      width: 34%;
      border: none;
    }

    .filter-button {
      height: 35px;
    }

    /* -----ENCOUNTER----- */
    .encounter-button {
      font-size: 16px;
    }
}